<template>
    <div class="app1">
        <form @submit.prevent>


            <input 
            v-bind:value="title" 
            @input="title = $event.target.value"
            class="input mini"
            type="text"
            placeholder="Название поста">
            
            <input 
            v-bind:value="body" 
            @input="body = $event.target.value"
            class="input" 
            type="text" 
            placeholder="Описание поста">


            <button class="btn" @click="createPost">Создать</button>
        </form>
        <div class="post" v-for="post in posts" v-bind:key="post" >
            <div><strong>Название:</strong> {{ post.title }}</div>
            <div><strong>Описание:</strong> {{ post.body }}</div>
        </div>
    </div>

</template>
<!-- ------------------------------- -->
<script>
export default {
    data() {
        return {
            posts: [
                // {id: 1, title: 'JavaScript', body: 'Описание поста'},
                // {id: 2, title: 'JavaScript2', body: 'Описание поста2'},
                // {id: 3, title: 'JavaScript3', body: 'Описание поста3'},
                // {id: 4, title: 'JavaScript4', body: 'Описание поста4'},
            ],
            title: '',
            body: '',
        }
    },
    methods: {
        createPost() {
            const newPost = {
                id: Date.now(),
                title: this.title,
                body: this.body,
            }
            this.posts.push(newPost);
            this.title = '';
            this.body = '';
        },
    }
}

</script>
<!-- ------------------------------- -->
<style>
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

form {
    display: flex;
    flex-direction: column;
    padding: 0 100px;
}
.app1 {
    padding: 20px;
}

.post {
    padding: 15px;
    border: 2px solid teal;
    margin-top: 15px;
}
.input {
    width: 100%;
    border: 1px solid teal;
    padding: 30px 15px;
    margin-top: 10px;
}
.mini {
    padding: 10px 15px;
}
.btn {
    margin-top: 15px;
    align-self: flex-end;
    padding: 10px 15px;
    background: none;
    color: teal;
    border: 1px solid teal;
}

</style>